li:hover::after {\n  width: 3rem;\n}\n\n/* Hero Jumbotron */\n.hero {\n  display: flex;\n  align-items: center;\n  min-height: 500px;\n  width: 100%;\n  text-align: center;\n  background-image: url('../public/images/heros/hero-image_2.jpg');\n  background-size: cover;\n  background-position: center;\n  background-color: var(--clr-primary);\n}\n\n.hero-inner {\n  margin: 2rem auto;\n  max-width: 600px;\n}\n\n.hero-title {\n  color: var(--clr-primary);\n  text-shadow: 0 6px 6px rgba(0, 0, 0, 0.5);\n  font-weight: 800;\n  font-size: 36px;\n}\n\n.hero-tagline {\n  color: var(--clr-primary);\n  text-shadow: 0 6px 6px rgba(0, 0, 0, 0.5);\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 700;\n}\n\n/* Main Section */\nmain > h1 {\n  margin: 4rem 0 3rem 0;\n  text-align: center;\n  color: var(--clr-blue-zodiac);\n}\n\n.content-restaurant {\n  margin: 1rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 15px;\n  flex-wrap: wrap;\n}\n\n.headline {\n  width: 400px;\n  height: auto;\n  position: relative;\n  background: var(--clr-primary);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  border-radius: var(--radius);\n}\n\n.headline > img {\n  width: 100%;\n  height: 200px;\n  object-fit: cover;\n  border-radius: 0.5rem 0.5rem 0 0;\n}\n\n.cityRender,\n.ratingRender {\n  position: absolute;\n  padding: 0.3rem;\n  text-align: center;\n  color: var(--clr-primary);\n  background-color: var(--clr-better-sweet);\n  border-radius: 0;\n}\n\n.cityRender {\n  top: 0.5rem;\n  left: 0;\n}\n\n.ratingRender {\n  top: 10rem;\n  right: 0;\n}\n\n.descriptionRender {\n  width: auto;\n  height: 6rem;\n  overflow-y: hidden;\n}\n\n.headline-content {\n  padding: 1rem;\n}\n\n.headline-story {\n  margin: 0 auto;\n  padding: 2rem;\n  width: 800px;\n  background-color: var(--clr-primary);\n  border-radius: var(--radius);\n}\n\n.content-story {\n  margin-top: 0.5rem;\n}\n\n.headline-story > p, .headline-story > h1, .headline-story > .content-story {\n  text-align: center;\n}\n\n.headline-story > p {\n  padding: 0 5rem;\n}\n\n.image-story-one {\n  width: 200px;\n  height: auto;\n  border-radius: var(--radius);\n}\n\n.image-story-two {\n  width: 200px;\n  height: auto;\n  border-radius: var(--radius);\n}\n\n/* Footer */\nfooter h3 {\n  padding: 2rem;\n  text-align: center;\n  color: var(--clr-blue-zodiac);\n  background-color: var(--clr-egg-sour);\n}\n\n/* Skip content link */\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: var(--clr-better-sweet);\n  color: var(--clr-primary);\n  padding: 8px;\n  z-index: 100;\n  transition: top 0.3s ease-in-out;\n\n  &:focus {\n    top: 0;\n  }\n}\n"],"sourceRoot":""}]);
